<template>
    <main id="main">
        <div class="container flexBox">                
            <div class="BigBox">
                <div class="slickBox">
                    <div>
                        <a href="editorial.html">
                            <img src="../assets/images/Editorial_01.jpg" alt="Editorial" title="Editorial">
                        </a>
                    </div>
                    <div>
                        <a href="editorial.html">
                            <img src="../assets/images/Editorial_02.png" alt="Editorial" title="Editorial">
                        </a>
                    </div>
                    <div>
                        <a href="editorial.html">
                            <img src="../assets/images/Editorial_03.png" alt="Editorial" title="Editorial">
                        </a>
                    </div>
                    <div>
                        <a href="makeup.html">
                            <img src="../assets/images/Makeup_01.png" alt="Makeup" title="Makeup">
                        </a>
                    </div>
                    <div>
                        <a href="makeup.html">
                            <img src="../assets/images/Makeup_02.png" alt="Makeup" title="Makeup">
                        </a>
                    </div>
                    <div>
                        <a href="mens_grooming.html">
                            <img src="../assets/images/Mens_grooming_01.png" alt="Men's grooming" title="Men's grooming">
                        </a>
                    </div>
                    <div>
                        <a href="mens_grooming.html">
                            <img src="../assets/images/Mens_grooming_02.png" alt="Men's grooming" title="Men's grooming">
                        </a>
                    </div>
                    <div>
                        <a href="wedding.html">
                            <img src="../assets/images/Wedding_01.png" alt="Wedding" title="Wedding">
                        </a>
                    </div>
                    <div>
                        <a href="wedding.html">
                            <img src="../assets/images/Wedding_02.png" alt="Wedding" title="Wedding">
                        </a>
                    </div>
                </div>
            </div>                
        </div>
    </main>
</template>

<script>
export default {
    methods: {
        sliderFn(){
            $('.slickBox').slick({
                autoplay: true,
                autoplaySpeed: 3000,
                speed: 500,
                dots: false,
                arrows: false,
                pauseOnFocus: false,
                adaptiveHeight: true
            })
        }
    },
    mounted(){
        this.sliderFn()
    }
}
</script>

<style>
/* main
------------------------------------------------------------------------------*/
#main{
    position: absolute;
    left: 0;
    z-index: 2;
    background-color: #ffffff;    
    width: 100%;
    height: 100%;
    min-height: calc(500px + 1rem);
    overflow: hidden;
    transition: all 0.5s ease;
}
#main .container{
    position: relative;
    justify-content: center;
    align-items: center;
}
.BigBox{
    width: 70%;
    max-width: 1150px;
    height: auto;
}
.slickBox{
    max-height: 100%;
    overflow: hidden;
    box-shadow: 00px 00px 100px rgba(255,255,255,1);
    border: 3px solid rgba(255,255,255,0.7);
    background-color: rgba(255,255,255,0.8);
}

.slickBox img{
    user-select: none;
}
.slickBox a{
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
}

/* media
----------------------------------------------------------------------------*/
/* 各解析度原始單位 */
@media screen and (max-width: 768px) {
    .BigBox{ width: calc(100% - 30px);}
    .index .mainLogo{ width: 3.2rem; top: 0.2rem; left: 0.2rem;}
}
@media screen and (max-width: 585px) {
    .index .mainLogo{ width: 2.4rem;}
}

</style>



