$(function(){
    
    var loadTxt = $('.loadTxt')
    var ldTT = ['L','o','a','d','i','n','g',' .',' .',' .','']
    var text = ''
//    alert(ldTT.length)
    var i = 0;//set your counter to 1
    function myLoop () {//create a loop function
//        loadTxt.fadeOut(0)
        setTimeout(function () {//call a 3s setTimeout when the loop is called            
            text += ldTT[i];//your code here
            loadTxt.html(text)
            i++;//increment the counter
            if (i == ldTT.length-1) {//if the counter <10, call the loop function
                i = 0
                text = ''
                myLoop()
            }else{
                myLoop();//.. again which will trigger another 
            }
        }, 600)
    }
    myLoop();//start the loop
    
    
    
    
    var toggleBtn = $('.toggleBtn');
    
    // 側邊 nav 選單
    toggleBtn.on('click', function(){                
        if(!$(this).hasClass('open')){
            $(this).addClass('open')
            $('#app').addClass('mainOpen')
            $('#main, footer').css({left: '-4.5rem'})
            $('.mask').css({'z-index': 99, right: '4.5rem'})

        }else{
            $(this).removeClass('open')
            $('#app').removeClass('mainOpen')
            $('#main, footer').css({left: ''})
            $('.mask').css({'z-index': '', right: ''})
        }

    })
    
    /* m 版側邊選單 */
    function nav_M(){
        var wHH = $(window).height()
        if( navigator.userAgent.match(/Android/i)
            || navigator.userAgent.match(/webOS/i)
            || navigator.userAgent.match(/iPhone/i)
            || navigator.userAgent.match(/iPad/i)
            || navigator.userAgent.match(/iPod/i)
            || navigator.userAgent.match(/BlackBerry/i)
            || navigator.userAgent.match(/Windows Phone/i)
        ){
            if(wHH <= 480){
                $('nav').css({'overflow-y': 'scroll'})
//                $('.mainOpen .toggleBtn').css({ right: '3.5rem'})
                $('nav > ul').css({'padding-top': '2rem','padding-bottom': '1rem'})
            }
        } 
    }
    
    
    $(window).resize(function(){
        
//        alert(wHH)
        nav_M()
    }).resize()
    
    
    
    // 每頁的差異
//    var pageName = location.pathname
    var pageName = $('.wrapper').attr('id')
    var pageTlt = $('.pageTlt')
//    alert(pageName)
    
    switch(pageName){
        case 'index':
            pageTlt.text('')
            break;
        case 'aboutme':
            pageTlt.text('About Me')
            $('.aboutBtn').addClass('active')
            break;
        case 'advertising':
            pageTlt.text('Advertising')
            $('.adverBtn').addClass('active')
            break;
        case 'editorial':
            pageTlt.text('Editorial')
            $('.ediBtn').addClass('active')
            break;
        case 'makeup':
            pageTlt.text('Makeup')
            $('.makeupBtn').addClass('active')
            break;
        case 'beauty':
            pageTlt.text('Beauty')
            $('.makeupBtn').addClass('active')
            break;
        case 'conceptual_beauty':
            pageTlt.text('Conceptual Beauty')
            $('.makeupBtn').addClass('active')
            break;
        case 'mens_grooming':
            pageTlt.text("Men's grooming")
            $('.groomingBtn').addClass('active')
            break;
        case 'videos':
            pageTlt.text('Videos')
            $('.videoBtn').addClass('active')
            break;
        case 'wedding':
            pageTlt.text('Wedding')
            $('.weddingBtn').addClass('active')
            break;
    }
    
    
});
$(window).on('load', function(){
    $('.loadingBar').delay(1000).fadeOut(300)
})


