<style>
/* main
------------------------------------------------------------------------------*/
#main{
    position: relative; left: 0; z-index: 2;
    background-color: #ffffff;    
    width: 100%; height: 100%; min-height: calc(500px + 1rem);
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease;
    padding-bottom: 1rem
}
.advertising #main{ min-height: calc(900px + 1rem);}
#main .container{
    position: relative;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
}


.picBox{
    width: 80%; max-width: 1200px;
    margin-top: 2rem;
}

.grid-item{
    width: calc(33.3333% - 6px);
    margin: 3px;
    cursor: pointer;
    overflow: hidden;
    position: relative;
    z-index: 0;
}
.grid-item:before{
    content: ''; display: block;
    width: 100%; height: 100%;
    position: absolute; top: 0; left: 0;
    background-color: rgba(255,255,255,0.3);
    opacity: 0; z-index: 1;
    -webkit-transition: all 0.35s ease;
    transition: all 0.35s ease;
}
.grid-item:hover:before{ opacity: 1;}
.grid-item img{
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;    
}
.grid-item:hover img{
    -webkit-transform: scale(1.05);
    transform: scale(1.05);
}




/* slickBox
----------------------------------------------------------------------*/
.slickBlock{
    position: fixed; top: 0; left: 0;
    width: 100%; height: 100%;
    background-color: rgba(0,0,0,0.7);
    z-index: -99999;
    opacity: 0;
    transition: all 0.5s ease;
}
.slickBlock.open{ z-index: 99999; opacity: 1;}
.slicBig{
    width: 80%; height: 80%;
    position: absolute; top: 10%; left: 10%;
}
.slickBlock .flexBox{
    justify-content: center;
    align-items: center;
    width: 100%; height: 100%;
}
.slickBox{    
    width: 100%; height: 100%;    
}
.slickBox .slickImg{
    width: 100%; height: 100%;
    position: relative;
}
.slickBox .slickImg div{
    position: absolute; top: 0; left: 0;
    width: 100%; height: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    justify-content: center;
    align-items: center;
    
}


/* slickBlock_closeBtn */
.slickBlock_closeBtn{
    position: absolute; top: -0.5rem; right: -0.4rem;
    width: 1rem; height: 1rem;
    background-color: #ffffff; border-radius: 50%;
    border: 4px solid #3f3f3f;
    cursor: pointer;
    transform: rotate(45deg);
}
.slickBlock_closeBtn span{
    display: block;
    width: 80%; height: 2px; background-color: #000000;
    position: absolute; top: 49%; left: 10%;
}
.slickBlock_closeBtn span:before{
    content: ''; display: block;
    width: 100%; height: 100%; background-color: #000000;
    transform: rotate(90deg);
}


@media screen and (max-width: 980px) {
    .grid-item{ width: calc(50% - 6px);}
}
@media screen and (max-width: 768px) {
    .slickBlock_closeBtn{
        top: -0.3rem; right: -0.2rem;
        width: 0.7rem; height: 0.7rem;
    }
} 
@media screen and (max-width: 585px) {
    .grid-item{ width: 100%; margin: 0 0 6px 0;}
}
</style>



